// ** React Imports
import { Link, useParams } from 'react-router-dom'

// ** Third Party Components
import classnames from 'classnames'
import PerfectScrollbar from 'react-perfect-scrollbar'
import { Mail, Send, Edit2, Star, Info, Trash } from 'react-feather'

// ** Reactstrap Imports
import { Button, ListGroup, ListGroupItem, Badge } from 'reactstrap'

const Sidebar = props => {
  // ** Props
  const { sidebarOpen, setOpenMail, refresList } =
    props

  // ** Vars
  const params = useParams()

  const handleLabel = label => {
    setOpenMail(false)
    refresList(label)
  }

  // ** Functions To Active List Item
  const handleActiveItem = value => {
    if ((params.folder && params.folder === value) || (params.label && params.label === value)) {
      return true
    } else {
      return false
    }
  }

  return (
    <div
      className={classnames('sidebar-left', {
        show: sidebarOpen
      })}
    >
      <div className='sidebar'>
        <div className='sidebar-content email-app-sidebar'>
          <div className='email-app-menu'>
            <PerfectScrollbar className='sidebar-menu-list' options={{ wheelPropagation: false }}>
              <ListGroup tag='div' className='list-group-messages'>
                <ListGroupItem>Email Alerts</ListGroupItem>
                <ListGroupItem
                  className='cursor-pointer'
                  onClick={() => handleLabel('')}
                  action
                  active={handleActiveItem('sent')}
                >
                  <Send size={14} className='me-75 text-primary' />
                  <span className='align-middle'>Sent</span>
                </ListGroupItem>
              </ListGroup>
              <h6 className='section-label mt-1 mb-1 px-2'>Labels</h6>
              <ListGroup tag='div' className='list-group-labels'>
                <ListGroupItem
                  className='cursor-pointer'
                  onClick={() => handleLabel('digital-signature')}
                  active={handleActiveItem('digital-signature')}
                  action
                >
                  <span className='bullet bullet-sm bullet-success me-1'></span>
                  DSC Reminder
                </ListGroupItem>
                <ListGroupItem
                  className='cursor-pointer'
                  onClick={() => handleLabel('sales')}
                  active={handleActiveItem('sales')}
                  action
                >
                  <span className='bullet bullet-sm bullet-primary me-1'></span>
                  Invoice
                </ListGroupItem>
                <ListGroupItem
                  className='cursor-pointer'
                  onClick={() => handleLabel('paymentack')}
                  active={handleActiveItem('paymentack')}
                  action
                >
                  <span className='bullet bullet-sm bullet-warning me-1'></span>
                  Payment Ack
                </ListGroupItem>
                <ListGroupItem
                  className='cursor-pointer'
                  onClick={() => handleLabel('compliance-alerts')}
                  active={handleActiveItem('compliance-alerts')}
                  action
                >
                  <span className='bullet bullet-sm bullet-danger me-1'></span>
                  Complinace Alerts
                </ListGroupItem>
              </ListGroup>
            </PerfectScrollbar>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Sidebar
