// ** React Imports
import { Fragment, useEffect, useState } from 'react'

// ** Mail Components Imports
import MailCard from './MailCard'
import MailDetails from './MailDetails'
import ReactPaginate from 'react-paginate'
// ** Utils
import { formatDateToMonthShort } from '@utils'

// ** Third Party Components
import PerfectScrollbar from 'react-perfect-scrollbar'
import { Menu, Search } from 'react-feather'

// ** Reactstrap Imports
import {
  Card,
  CardBody,
  Input,
  InputGroup,
  InputGroupText
} from 'reactstrap'
import axios from '@src/configs/axios/axiosConfig'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
const Mails = props => {
  // ** Props
  const {
    query,
    store,
    openMail,
    setQuery,
    dispatch,
    selectMail,
    updateMails,
    setOpenMail,
    paginateMail,
    selectAllMail,
    setSidebarOpen,
    updateMailLabel,
    resetSelectedMail,
    filterList
  } = props

  const { selectedMails } = store
  const [currentMail, setCurrentMail] = useState(null)

  // ** States
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const [mails, setMails] = useState([])
  const [searchTerm, setSearchTerm] = useState('')
  const [total, setTotal] = useState(0)
  const [currentPage, setCurrentPage] = useState(1)
  const rowsPerPage = 10

  // ** Variables
  const labelColors = [
    'success',
    'primary',
    'warning',
    'danger'
  ]

  // ** Handles Update Functions
  const handleMailClick = id => {
    setCurrentMail(mails.find((obj) => id === obj.id))
    setOpenMail(true)
  }

  const MailClose = val => {
    if (!val) {
      setOpenMail(false)
      setCurrentMail(null)
    }
  }

  // ** Handles Folder Update
  const handleFolderUpdate = (e, folder, ids = selectedMails) => {
    e.preventDefault()
    dispatch(updateMails({ emailIds: ids, dataToUpdate: { folder } }))
    dispatch(resetSelectedMail())
  }

  // ** Handles Label Update
  const handleLabelsUpdate = (e, label, ids = selectedMails) => {
    e.preventDefault()
    dispatch(updateMailLabel({ emailIds: ids, label }))
    dispatch(resetSelectedMail())
  }

  // ** Handles Mail Read Update
  const handleMailReadUpdate = (arr, bool) => {
    dispatch(updateMails({ emailIds: arr, dataToUpdate: { isRead: bool } })).then(() => dispatch(resetSelectedMail()))
    dispatch(selectAllMail(false))
  }

  // ** Handles Move to Trash
  const handleMailToTrash = ids => {
    dispatch(updateMails({ emailIds: ids, dataToUpdate: { folder: 'trash' } }))
    dispatch(resetSelectedMail())
  }

  const getEmails = (params) => {
    axios.post('/emailalerts/list', params)
      .then((res) => {
        setMails(res.data.emailalerts.emailalerts)
        setTotal(res.data.emailalerts.total)
      })
      .catch((err) => {
        console.log(err)
      })
  }

  useEffect(() => {
    getEmails({
      q: searchTerm,
      perPage: rowsPerPage,
      page: currentPage,
      filter: filterList
    })
  }, [filterList])


  const handlePagination = async page => {
    setBlock(true)
    await getEmails({
      q: searchTerm,
      perPage: rowsPerPage,
      page: page.selected + 1,
      filter: filterList
    })
    setCurrentPage(page.selected + 1)
    setBlock(false)
  }

  // ** Renders Mail
  const renderMails = () => {
    if (mails.length) {
      return mails.map((mail, index) => {
        return (
          <MailCard
            mail={mail}
            key={index}
            dispatch={dispatch}
            selectMail={selectMail}
            updateMails={updateMails}
            labelColors={labelColors}
            selectedMails={selectedMails}
            handleMailClick={handleMailClick}
            handleMailReadUpdate={handleMailReadUpdate}
            formatDateToMonthShort={formatDateToMonthShort}
          />
        )
      })
    }
  }

  const CustomPagination = () => {
    const count = Number(Math.ceil(total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end mt-50 pe-1'}
      />
    )
  }

  return (
    <Fragment>

      <div className='email-app-list'>
        <div className='app-fixed-search d-flex align-items-center'>
          <div className='sidebar-toggle d-block d-lg-none ms-1' onClick={() => setSidebarOpen(true)}>
            <Menu size='21' />
          </div>
          <div className='d-flex align-content-center justify-content-between w-100'>
            <InputGroup className='input-group-merge'>
              <InputGroupText>
                <Search className='text-muted' size={14} />
              </InputGroupText>
              <Input
                id='email-search'
                placeholder='Search email'
                value={query}
                onChange={e => { setSearchTerm(e.target.value); setQuery(e.target.value) }}
              />
            </InputGroup>
          </div>
        </div>

        <PerfectScrollbar className='email-user-list' options={{ wheelPropagation: false }}>
          <UILoader blocking={block} loader={<Loader />}>
            {mails.length ? (
              <ul className='email-media-list'>{renderMails()}</ul>
            ) : (
              <div className='no-results d-block'>
                <h5>No Items Found</h5>
              </div>
            )}
          </UILoader>
        </PerfectScrollbar>
        {currentMail === null &&
          <Card>
            <CardBody className='py-0 px-0'>
              <CustomPagination />
            </CardBody>
          </Card>
        }
      </div>
      <MailDetails
        openMail={openMail}
        dispatch={dispatch}
        mail={currentMail}
        labelColors={labelColors}
        labelName={filterList}
        setOpenMail={setOpenMail}
        updateMails={updateMails}
        MailClose={MailClose}
        paginateMail={paginateMail}
        updateMailLabel={updateMailLabel}
        handleMailToTrash={handleMailToTrash}
        handleFolderUpdate={handleFolderUpdate}
        handleLabelsUpdate={handleLabelsUpdate}
        handleMailReadUpdate={handleMailReadUpdate}
        formatDateToMonthShort={formatDateToMonthShort}
      />
    </Fragment>
  )
}

export default Mails
