// ** Custom Components & Plugins
import classnames from 'classnames'
import { Star, Paperclip } from 'react-feather'

// ** Custom Component Import
import Avatar from '@components/avatar'

import moment from 'moment'

const MailCard = props => {
  // ** Props
  const {
    mail,
    labelColors,
    handleMailClick,
    handleMailReadUpdate
  } = props
    
  // // ** Function to render labels
  // const renderLabels = arr => {
  //   if (arr && arr.length) {
  //     return arr.map(label => (
  //       <span key={label} className={`bullet bullet-${labelColors[label]} bullet-sm mx-50`}></span>
  //     ))
  //   }
  // }

  // ** Function to handle read & mail click
  const onMailClick = () => {
    handleMailClick(mail.id)
    handleMailReadUpdate([mail.id], true)
  }

  const dateFormat = (val) => {
    return moment(val, 'x').format('MMM DD, Y')
  }

  return (
    <li onClick={() => onMailClick(mail.id)} className={classnames('d-flex user-mail p-1')}>
      <div className='mail-left pe-50'>
        <Avatar initials
          className='me-1'
          color={labelColors[0]}
          content={mail.subject ? mail.subject?.charAt(0).toUpperCase() : ''} />
      </div>
      <div className='mail-body'>
        <div className='mail-details'>
          <div className='mail-items'>
            <h5 className='mb-25'>{mail.subject}</h5>
          </div>
          <div className='mail-meta-item'>
            {mail.attachments && mail.attachments.length ? <Paperclip size={14} /> : null}
            <span className="bullet bullet-success bullet-sm mx-50"></span>
            <span className='mail-date'>{dateFormat(mail.createdon)}</span>
          </div>
        </div>
        <div className='mail-message'>
          <div className='mail-message text-truncate' dangerouslySetInnerHTML={{ __html: mail.content.replace(/(<([^>]+)>)/ig, '') }}></div>
        </div>
      </div>
    </li>
  )
}

export default MailCard
