// ** React Imports
import { Fragment, useEffect } from 'react'
// ** Custom Components
import Avatar from '@components/avatar'

// ** Third Party Components
import classnames from 'classnames'

import {
  ChevronLeft, ChevronRight
} from 'react-feather'
import PerfectScrollbar from 'react-perfect-scrollbar'

// ** Reactstrap Imports
import {
  Row,
  Col,
  Badge,
  Card,
  Table,
  CardBody,
  CardFooter,
  CardHeader
} from 'reactstrap'
import moment from 'moment'

const MailDetails = props => {
  // ** Props
  const {
    mail,
    openMail,
    labelColors,
    MailClose,
    setOpenMail,
    labelName
  } = props
  //** Renders Labels
  const renderLabels = val => {

    return <Badge color={`light-${labelColors[0]}`} className='me-50 text-capitalize' pill>
      {val.toUpperCase()}
    </Badge>

  }

  useEffect(() => {
    renderLabels(labelName)
  }, [labelName])

  const formatHTML = (myString) => {

    const divElement = document.createElement('div')
    divElement.innerHTML = myString

    const elements = divElement.getElementsByTagName("div")
    const elementtd = divElement.getElementsByTagName("td")
    for (let i = 0; i < elements.length; i++) {
      if ((elements[i].getAttribute('style') || '')) {
        elements[i].removeAttribute('style')
      }
    } for (let i = 0; i < elementtd.length; i++) {
      if ((elementtd[i].getAttribute('style').includes('border-bottom') || '')) {
        elementtd[i].removeAttribute('style')
      }
    }
    return divElement.innerHTML
  }

  const formatDate = (val) => {
    return moment(val, 'x').format('MMM DD, Y')
  }

  // ** Renders Messages
  const renderMessage = obj => {
    return (
      <Card>
        <CardHeader className='email-detail-head'>
          <div className='user-details d-flex justify-content-between align-items-center flex-wrap'>
            <Avatar initials content={obj.client ? obj.client?.charAt(0) : 'T'} color='primary' className='me-1' />
            <div className='mail-items'>
              <h5 className='mb-0'>{obj.client}</h5>
              <span className='me-25'>{obj.tomail}</span>
            </div>
          </div>
          <div className='mail-meta-item d-flex align-items-center'>
            <small className='mail-date-time text-muted'>{formatDate(obj.createdon)}</small>
          </div>
        </CardHeader>
        <CardBody className='mail-message-wrapper pt-2'>
          <div className='mail-message' dangerouslySetInnerHTML={{ __html: formatHTML(obj.content) }}></div>
        </CardBody>
      </Card>
    )
  }

  const handleGoBack = () => {
    setOpenMail(false)
    MailClose(false)
  }
  
  return (
    <div
      className={classnames('email-app-details w-100', {
        show: openMail
      })}
    >
      {mail !== null && mail !== undefined ? (
        <Fragment>
          <div className='email-detail-header'>
            <div className='email-header-left d-flex align-items-center'>
              <span className='go-back me-1' onClick={handleGoBack}>
                <ChevronRight size={20} />
              </span>
              <h4 className='email-subject mb-0'>{mail.subject}</h4>
            </div>
          </div>

          <PerfectScrollbar className='email-scroll-area' options={{ wheelPropagation: false }}>
            <Row>
              <Col sm='12'>
                <div className='email-label'>{renderLabels(mail.maillable || 'Test')}</div>
              </Col>
            </Row>
            <Row>
              <Col sm='12'>{renderMessage(mail)}</Col>
            </Row>
          </PerfectScrollbar>
        </Fragment>
      ) : null}
    </div>
  )
}

export default MailDetails
